.content {
  background: url("../i/back-1.png") center / cover no-repeat;
  @include e(box) {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 4fr;
    justify-items: center;
    margin: 0 auto;
  }
  @include e(title) {
    margin: 46px 0 20px 0;
  }
  @include e(block) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-gap: 15px;
    margin: 0 0 18px 0;
  }
  @include e(text) {
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    color: $white;
    margin: 0 0 28px 0;
  }
  @include e(button) {
    width: 200px;
    height: 46px;
    background: $goldBtn;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: $black1;
    cursor: pointer;
    margin: 0 0 63px 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    transition-duration: 0.7s;
    &:hover {
      box-shadow: 0px 0px 48px rgba(240, 192, 92, 0.54);
    }
  }
}
.houses {
  text-transform: uppercase;
  font-weight: 900;
  font-size: 57px;
  line-height: 60px;
  color: $white;
  text-align: center;
  margin: 0 0 13px 0;
}
.ready {
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
  color: $white;
  text-align: center;
  margin: 0;
}
.item {
  display: flex;
  height: 60px;
  background: rgba(255, 255, 255, 0.71);
  padding: 0 5px;
  align-items: center;
  justify-content: center;
  text-align: center;
  transition-duration: 0.7s;
  &:hover {
    background: rgba(221, 160, 70, 1);
    cursor: pointer;
  }
}
.item-link {
  font-weight: 700;
  font-size: 18px;
  color: $black2;
  text-decoration: none;
}
.hidden {
  display: none;
}
.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  overflow-y: auto;
  overflow-x: hidden;
  transition: all 0.8s ease 0s;
  @include e(body) {
    padding: 40px 10px 30px;
    min-height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.8s ease 0s;
  }
  @include e(content) {
    padding: 25px 10px 45px;
    background-color: $white;
    width: 100%;
    max-width: 1060px;
    transition: all 0.8s ease 0s;
    opacity: 0;
    transform: perspective(600px) translate(0px, -100%) rotateX(45deg);
  }
  @include e(close) {
    position: absolute;
    right: 10px;
    top: 10px;
    font-size: 20px;
    color: $black1;
    text-decoration: none;
  }
  @include e(title) {
    font-weight: 400;
    font-size: 24px;
    line-height: 28px;
    text-align: center;
    color: $black1;
    margin-bottom: 30px;
  }
  @include e(text) {
    font-weight: 600;
    font-size: 18px;
    line-height: 21px;
    text-align: center;
    margin-bottom: 18px;
  }
  @include e(radio-case) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 500px;
    margin: 0 auto 40px;
    @include m(bottom) {
      width: 224px;
    }
  }
  @include e(radio-item) {
    position: relative;
    font-weight: 400;
    font-size: 14px;
    line-height: 28px;
    padding-left: 33px;
    margin: 0 10px 7px 0;
    &::before {
      content: "";
      position: absolute;
      width: 28px;
      height: 28px;
      top: 0;
      left: 0;
      background-color: rgb(219, 219, 219);
      box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.2);
      transition: all 0.3s ease 0s;
    }
    &::after {
      content: "";
      position: absolute;
      display: block;
      width: 15px;
      height: 15px;
      background: url(../i/check.png) 0 0 / cover no-repeat;
      top: 6px;
      left: 6px;
    }
    &:last-of-type {
      margin-right: 0;
    }
  }
  @include e(btn-case) {
    text-align: center;
  }
  @include e(tel) {
    padding: 10px 20px;
    border: none;
    width: 100%;
    max-width: 430px;
    height: 50px;
    background-color: #f4f4f4;
    margin: 0 20px 10px 0;
    outline: none;
  }
  @include e(btn) {
    width: 200px;
    height: 50px;
    background: $goldBtn;
    color: $black1;
    border: none;
    cursor: pointer;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    transition-duration: 0.7s;
    &:hover {
      box-shadow: 0px 0px 48px rgba(240, 192, 92, 0.54);
    }
  }
}
.popup__radio-item input {
  display: none;
}
.popup__radio-item.active::before {
  background-color: $orange;
}
.slider {
  margin-bottom: 42px;
  @include e(unit) {
    position: relative;
    text-align: center;
    height: 160px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    max-width: 136px;
    &:hover {
      cursor: pointer;
    }
    &:before {
      content: "";
      position: absolute;
      width: 28px;
      height: 28px;
      border-radius: 50%;
      top: 0;
      left: 54px;
      background-color: rgb(219, 219, 219);
      box-shadow: -1px 3px 6px rgba(0, 0, 0, 0.16);
      transition: all 1s ease 0s;
    }
    &:after {
      content: "";
      position: absolute;
      display: block;
      width: 15px;
      height: 15px;
      background: url(../i/check.png) 0 0 / cover no-repeat;
      top: 6px;
      left: 61px;
    }
  }
  @include e(img) {
    width: 136px;
    margin-bottom: 10px;
    box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.2);
  }
  @include e(description) {
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    margin: 0;
    height: 38px;
  }
}
.slider__unit input {
  display: none;
}
.slider__unit.active img {
  background: $orange;
  transition: all 0.5s ease 0s;
}
.slider__unit.active::before {
  background-color: $orange;
}
.slider .slick-dots {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
}
.slider .slick-dots li {
  list-style: none;
  margin: 0 5px;
}
.slider .slick-dots li button {
  border: 1px solid black;
  outline: none;
  border-radius: 50%;
  font-size: 0;
  width: 10px;
  height: 10px;
  background-color: $white;
  padding: 0;
}
.slider .slick-dots li.slick-active button {
  background-color: $orange;
}
.popup.open {
  opacity: 1;
  visibility: visible;
}
.popup.open .popup__content {
  transform: perspective(600px) translate(0px, 0%) rotateX(0deg);
  opacity: 1;
}
