.about {
  background: $gray;
  @include e(case) {
    width: 100%;
  }
  @include e(heading) {
    font-weight: 400;
    font-size: 48px;
    line-height: 56px;
    margin: 0 0 56px;
    text-align: center;
    color: $black2;
    @include m(bold) {
      font-weight: 700;
    }
  }
}
.grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  grid-gap: 28px;
  justify-items: center;
  width: 100%;
  max-width: 1060px;
  margin: 0 auto 50px;
  @include e(holder) {
    display: flex;
    margin: 0 0 40px 0;
    max-width: 340px;
  }
  @include e(image-box) {
    width: 70px;
    height: 70px;
    background: $white;
    padding: 14px;
  }
  @include e(image) {
    width: 42px;
    height: 42px;
  }
  @include e(content) {
    margin: 0 0 0 15px;
  }
  @include e(head) {
    font-weight: 900;
    font-size: 20px;
    line-height: 23px;
    margin: 0 0 15px 0;
  }
  @include e(text) {
    font-weight: 400;
    font-size: 18px;
    line-height: 27px;
    margin: 0;
    @include m(bold) {
      font-weight: 700;
    }
  }
}
