.container {
  padding: 0 30px 0 30px;
}
.header {
  @include e(elements) {
    gap: 10px;
  }
  @include e(logo) {
    width: 100px;
    height: 25px;
    margin-right: 5px;
  }
  @include e(tel) {
    font-size: 16px;
    line-height: 21px;
    &::before {
      margin-right: 6px;
    }
  }
  @include e(ico) {
    height: 34px;
    width: 34px;
  }
}
.text-1 {
  font-size: 18px;
  line-height: 19px;
}
.text-2 {
  font-size: 21px;
  line-height: 24px;
}
.header__burger {
  margin-right: 10px;
}
.header__list {
  margin: 0;
  padding: 109px 20px 0 30px;
}
