.header {
  background-color: $black1;
  width: 100%;
  padding: 18px 0;
  @include e(logo) {
    position: relative;
    z-index: 3;
    width: 85px;
    height: 22px;
    margin-right: 5px;
  }
  @include e(tel) {
    position: relative;
    z-index: 3;
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    color: $white;
    text-decoration: none;
    &::before {
      content: "";
      background: url(../i/phone-ico.png) no-repeat;
      width: 20px;
      height: 20px;
      color: $orange;
      display: inline-block;
      line-height: 16px;
      vertical-align: middle;
      margin-right: 3px;
    }
  }
  @include e(elements) {
    width: 100%;
    max-width: 1060px;
    justify-content: space-between;
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
  }
  @include e(description) {
    position: relative;
    z-index: 3;
  }
  @include e(link-ico) {
    position: relative;
    z-index: 3;
    margin-right: 10px;
    &:last-of-type {
      margin-right: 0;
    }
  }
  @include e(ico) {
    height: 34px;
    width: 34px;
  }
}
.container {
  padding: 0 20px 0 20px;
  display: flex;
  margin: 0 auto;
}
// container!!!
.text-1 {
  color: $white;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  margin: 0 0 0 0;
}
.text-2 {
  color: $white;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  margin: 0 0 0 0;
}
.text-3 {
  color: $orange;
}
.ico-box {
  flex-direction: flex-end;
}
.header {
  position: relative;
}
.header::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: $black1;
  z-index: 3;
}
.header__burger-case {
  display: flex;
  margin-bottom: 7px;
}
.header__burger {
  display: block;
  position: relative;
  width: 30px;
  height: 20px;
  z-index: 3;
  margin-right: 7px;
}
.header__burger::before,
.header__burger::after {
  content: "";
  background-color: $white;
  position: absolute;
  width: 100%;
  height: 2px;
  left: 0;
  transition: all 0.5s ease 0s;
}
.header__burger::before {
  top: 0;
}
.header__burger::after {
  bottom: 0;
}
.header__burger span {
  position: absolute;
  background-color: $white;
  left: 0;
  width: 100%;
  height: 2px;
  top: 9px;
  transition: all 0.5s ease 0s;
}
.header__burger.active::before {
  transform: rotate(45deg);
  top: 9px;
}
.header__burger.active::after {
  transform: rotate(-45deg);
  bottom: 9px;
}
.header__burger.active span {
  transform: scale(0);
}
.header__menu {
  position: fixed;
  top: -100%;
  width: 100%;
  max-width: 300px;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.95);
  transition: all 0.3s ease 0s;
  overflow: auto;
}
.header__menu.active {
  top: 0;
  height: 400px;
  transition: all 0.3s ease 0s;
}
.header__list {
  margin: 0;
  padding: 120px 20px 0;
}
.header__list li {
  list-style: none;
  margin-bottom: 30px;
}
.header__link {
  color: $black1;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 20px;
  line-height: 23px;
}
