.video {
  padding-bottom: 100px;
  @include e(box) {
    flex-direction: row;
    padding: 48px 30px 50px;
    align-items: flex-start;
  }
  @include e(text) {
    text-align: left;
    margin-right: 30px;
  }
  @include e(heading) {
    font-size: 36px;
    line-height: 50px;
  }
  @include e(description) {
    font-size: 24px;
    line-height: 33px;
    color: $white;
  }
}
