.content {
  padding: 110px 0 132px;
  @include e(box) {
    grid-template-columns: 2fr;
    grid-template-rows: 3fr;
    justify-items: flex-start;
    max-width: 1060px;
    width: 100%;
  }
  @include e(title) {
    grid-column: 1/3;
    grid-row: 1;
    align-self: flex-start;
    margin-top: 0;
  }
  @include e(block) {
    grid-column: 1;
    grid-row: 3;
    align-self: end;
    gap: 20px;
    margin: 0 auto;
  }
  @include e(text) {
    grid-column: 1;
    grid-row: 2;
    align-self: flex-end;
    font-weight: 400;
    font-size: 24px;
    line-height: 28px;
    text-align: left;
    margin-right: 50px;
  }
  @include e(button) {
    display: none;
  }
}
.houses {
  text-align: left;
}
.ready {
  font-weight: 700;
  font-size: 42px;
  line-height: 49px;
  text-align: left;
}
.order {
  padding: 45px 12px 45px;
  background: rgba(255, 255, 255, 0.71);
  width: 340px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-column: 2;
  grid-row: 2/4;
  justify-self: end;
  @include e(heading) {
    font-weight: 700;
    font-size: 32px;
    line-height: 38px;
    text-align: center;
    margin: 0 0 15px 0;
  }
  @include e(text) {
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    text-align: center;
    margin: 0 0 32px 0;
  }
  @include e(img) {
    width: 246px;
    height: 166px;
    margin: 0 0 18px 0;
    text-align: center;
  }
  @include e(btn) {
    width: 200px;
    height: 46px;
    background: $goldBtn;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: $black1;
    cursor: pointer;
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    transition-duration: 0.7s;
    &:hover {
      box-shadow: 0px 0px 48px rgba(240, 192, 92, 0.54);
    }
  }
}
.popup {
  @include e(content) {
    padding: 47px 75px 50px;
  }
}
