.director {
  background: url(../i/back-3.png) 0 0 / cover no-repeat;
  @include e(case) {
    padding: 70px 0 70px 0;
  }
  @include e(text) {
    font-weight: 400;
    font-size: 30px;
    line-height: 35px;
    text-align: center;
    @include m(bold) {
      font-weight: 700;
    }
  }
  @include e(picture-box) {
    background: url("../i/director.png") 0 0 / cover no-repeat;
    height: 579px;
    display: flex;
    justify-content: left;
    align-items: end;
    margin: 0 auto 50px;
    max-width: 708px;
  }
  @include e(sign) {
    margin: 0 0 29px 26px;
  }
  @include e(caption) {
    font-weight: 400;
    font-size: 18px;
    line-height: 27px;
    color: $white;
    margin: 0;
    @include m(bold) {
      font-weight: 700;
    }
  }
}
.grid-shell {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  justify-items: center;
  grid-gap: 30px;
  @include e(unit) {
    display: flex;
    padding: 5px 0;
    max-width: 340px;
  }
  @include e(checkmark) {
    height: 22px;
    width: 20px;
    margin-right: 16px;
  }
  @include e(specification) {
    margin: 0;
    @include m(bold) {
      font-weight: 700;
    }
  }
}
