.contact {
  @include e(case) {
    max-width: 1060px;
    margin: 0 auto;
  }
  @include e(heading) {
    font-size: 48px;
    line-height: 56px;
  }
}
.form {
  padding: 65px 40px 40px 40px;
  @include e(header) {
    font-size: 36px;
    line-height: 42px;
    margin: 0 0 30px 0;
  }
  @include e(text) {
    font-size: 22px;
    line-height: 33px;
    margin: 0 0 40px 0;
  }
}
.info {
  padding: 65px 40px 40px 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  @include e(head) {
    font-size: 36px;
    line-height: 42px;
    margin: 0 0 30px 0;
  }
  @include e(place) {
    margin-bottom: 30px;
    align-items: center;
    &:last-of-type {
      margin-bottom: 60px;
    }
  }
  @include e(text) {
    font-size: 20px;
    line-height: 23px;
  }
  @include e(number) {
    font-size: 36px;
    line-height: 42px;
  }
}
