.sponsors {
  @include e(case) {
    padding: 70px 0 70px 0;
  }
  @include e(heading) {
    font-weight: 400;
    font-size: 30px;
    line-height: 35px;
    text-align: center;
    margin-bottom: 40px;
    @include m(bold) {
      font-weight: 700;
    }
  }
  @include e(ico-box) {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
  }
  @include e(img-wrap) {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
