.footer {
  background: rgba(29, 26, 20, 1);
  @include e(case) {
    position: relative;
    width: 100%;
    padding: 60px 26px 60px 26px;
  }
  @include e(arrow-box) {
    position: absolute;
    right: 0;
    top: 60;
  }
  @include e(arrow-link) {
    text-decoration: none;
    transition-duration: 1s;
  }
  @include e(arrow) {
    &:hover {
      transform: scale(1.1);
    }
  }
  @include e(elements) {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    gap: 10px;
  }
  @include e(logo-box) {
    margin: 0 0 20px 0;
  }
  @include e(logo) {
    width: 140px;
    height: 36px;
    color: $white;
  }
  @include e(list-1) {
    margin: 0 0 25px 0;
  }
  @include e(header) {
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    color: $footerCol;
    margin: 0 0 20px 0;
    display: block;
    text-decoration: none;
  }
  @include e(item-1) {
    font-weight: 400;
    font-size: 16px;
    line-height: 21px;
    color: $footerCol;
    margin: 0 0 7px 0;
    display: block;
    text-decoration: none;
    &:last-of-type {
      margin: 0;
    }
  }
  @include e(list-2) {
    padding-bottom: 30px;
    border-bottom: 1px solid #2e2a22;
    margin: 0 0 25px 0;
  }
  @include e(item-2) {
    display: block;
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    color: $footerCol;
    margin: 0 0 10px;
    text-decoration: none;
    &:last-of-type {
      margin: 0;
    }
  }
  @include e(list-3) {
    margin: 0 0 25px 0;
  }
  @include e(item-3) {
    font-weight: 400;
    font-size: 16px;
    line-height: 21px;
    color: $footerCol;
    text-decoration: none;
    &:last-of-type {
      margin: 0;
    }
  }
  @include e(ico-holder) {
    display: flex;
    margin: 0 0 7px 0;
  }
  @include e(ico) {
    width: 20px;
    height: 20px;
    margin: 0 20px 0 0;
  }
  @include e(img-box) {
    display: flex;
    gap: 15px;
    margin-top: 25px;
  }
  @include e(img-holder) {
  }
  @include e(img) {
    width: 40px;
    height: 40px;
  }
}
.link {
  &:hover {
    color: $orange;
  }
}
