.director {
  @include e(case) {
    max-width: 1060px;
    margin: 0 auto;
  }
  @include e(holder) {
    display: flex;
    align-items: center;
  }
  @include e(picture-box) {
    min-width: 340px;
    margin: 0 30px 0 0;
  }
}
.grid-shell {
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  height: 470px;
}
