.contact {
  background: url("../i/back-5.png") 0 0 / cover no-repeat;
  display: block;
  @include e(case) {
    padding: 70px 0 70px 0;
    width: 100%;
  }
  @include e(heading) {
    font-weight: 700;
    font-size: 30px;
    line-height: 35px;
    text-align: center;
    color: $black1;
    margin: 0 0 38px 0;
  }
}
.form {
  background: rgba(239, 236, 234, 0.6);
  box-shadow: 0px 0px 74px rgba(39, 17, 10, 0.16);
  padding: 40px 26px 40px 26px;
  width: 100%;
  max-width: 520px;
  margin: 0 auto 40px;
  @include e(header) {
    font-weight: 900;
    font-size: 24px;
    line-height: 28px;
    margin: 0 0 24px 0;
    color: $black1;
  }
  @include e(text) {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    margin: 0 0 24px 0;
    color: $black1;
  }
  @include e(textarea) {
    width: 100%;
    height: 121px;
    padding: 13px;
    margin: 0 0 15px 0;
    background: rgba(255, 255, 255, 0.33);
    box-shadow: inset 1px 1px 12px rgba(0, 0, 0, 0.19);
    text-decoration: none;
    border: 0;
    outline: none;
  }
  @include e(input) {
    width: 100%;
    height: 50px;
    padding: 10px 13px;
    margin: 0 0 35px 0;
    background: rgba(255, 255, 255, 0.33);
    box-shadow: inset 1px 1px 12px rgba(0, 0, 0, 0.19);
    text-decoration: none;
    border: 0;
    outline: none;
  }
  @include e(btn) {
    display: block;
    width: 200px;
    height: 46px;
    background: $goldBtn;
    border: none;
    cursor: pointer;
  }
}
.info {
  background: rgba(239, 236, 234, 0.6);
  box-shadow: 0px 0px 74px rgba(39, 17, 10, 0.16);
  padding: 40px 26px 40px 26px;
  width: 100%;
  max-width: 520px;
  margin: 0 auto;
  @include e(head) {
    font-weight: 900;
    font-size: 24px;
    line-height: 28px;
    color: $black1;
    margin: 0 0 30px 0;
  }
  @include e(place) {
    display: flex;
    margin-bottom: 22px;
    &:last-of-type {
      margin-bottom: 40px;
    }
  }
  @include e(ico) {
    width: 20px;
    height: 20px;
    margin-right: 18px;
  }
  @include e(text) {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: $black1;
    margin: 0;
  }
  @include e(box) {
    width: 100%;
    margin-bottom: 22px;
    display: flex;
    gap: 5px;
  }
  @include e(img-box) {
    background: $white;
    min-width: 110px;
    width: 140px;
    text-align: center;
    padding: 8px 0 8px 0;
  }
  @include e(img) {
    width: 32px;
    height: 32px;
  }
  @include e(number) {
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    margin: 0;
  }
  @include e(btn) {
    display: block;
    width: 200px;
    height: 46px;
    background: $goldBtn;
    border: none;
    cursor: pointer;
  }
}
