.products {
  background: $gray;
  padding: 70px 0 50px 0;
  @include e(box) {
    margin: 0 auto;
    text-align: center;
  }
  @include e(title) {
    font-weight: 400;
    font-size: 30px;
    line-height: 35px;
    text-align: center;
    margin-bottom: 44px;
  }
  @include e(case) {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    grid-gap: 20px;
    justify-items: center;
  }
}
.object {
  display: flex;
  flex-direction: column;
  background: rgba(255, 255, 255, 0.6);
  max-width: 340px;
  padding-top: 54px;
  text-align: center;
  transition-duration: 1s;
  &:hover {
    background: $orange;
  }
  @include e(caption) {
    font-weight: 900;
    font-size: 28px;
    line-height: 33px;
    color: $black1;
    margin-bottom: 60px;
    height: 66px;
  }
  @include e(btn) {
    display: block;
    width: 200px;
    height: 46px;
    background: $goldBtn;
    border: none;
    cursor: pointer;
    margin: 0 auto 34px;
    transition-duration: 0.7s;
    &:hover {
      color: white;
      background: #333;
      box-shadow: inset 0 0 0 1px $white;
    }
  }
  @include e(img) {
    width: 100%;
    max-width: 340px;
    flex: 1;
  }
}
.bold {
  font-weight: 700;
}
.caption-1 {
  font-weight: 700;
  font-size: 30px;
  line-height: 30px;
  color: $black2;
}
.caption-2 {
  font-weight: 400;
  font-size: 24px;
  line-height: 28px;
  color: $black2;
}
