.galery {
  background: $gray;
  @include e(case) {
    padding-bottom: 70px;
  }
  @include e(heading) {
    font-weight: 400;
    font-size: 30px;
    line-height: 35px;
    text-align: center;
    margin: 0 0 38px;
    @include m(bold) {
      font-weight: 700;
    }
  }
  @include e(btn-box) {
    max-width: 120px;
    margin: 26px auto 35px;
  }
  @include e(btn) {
    display: block;
    width: 200px;
    height: 46px;
    background: $goldBtn;
    border: none;
    cursor: pointer;
    margin: 0 auto;
  }
  .grid-box {
    max-width: 1366px;
    position: relative;
    margin-bottom: 85px;
    @include e(unit) {
      text-align: center;
    }
    @include e(img) {
      width: 100%;
      max-width: 454px;
    }
  }
}
.grid-box .slick-arrow {
  position: absolute;
  top: 50%;
  z-index: 10;
  font-size: 0;
  width: 35px;
  height: 25px;
  outline: none;
  border: none;
}
.grid-box .slick-arrow.slick-prev {
  left: 25px;
  background: url("../i/arrow-left.svg") center / 100% no-repeat;
  &:hover {
    cursor: pointer;
    transform: scale(1.5);
    transition-duration: 0.3s;
  }
}
.grid-box .slick-arrow.slick-next {
  right: 25px;
  background: url("../i/arrow-right.svg") center / 100% no-repeat;
  &:hover {
    cursor: pointer;
    transform: scale(1.5);
    transition-duration: 0.3s;
  }
}
.grid-box .slick-dots {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
}
.grid-box .slick-dots li {
  list-style: none;
  margin: 0 5px;
}
.grid-box .slick-dots li button {
  border: 1px solid black;
  outline: none;
  border-radius: 50%;
  font-size: 0;
  width: 10px;
  height: 10px;
  background-color: $white;
  padding: 0;
}
.grid-box .slick-dots li.slick-active button {
  background-color: $orange;
}

/*   */
.slick-list {
  overflow: hidden;
}
/*   */
.slick-track {
  display: flex;
  justify-content: space-around;
}
