.video {
  background: $gray;
  padding-bottom: 70px;
  @include e(box) {
    background: url("../i/back-2.png") right no-repeat;
    background-color: rgba(29, 26, 20, 1);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 48px 8px 50px;
    max-width: 1060px;
    margin: 0 auto;
  }
  @include e(text) {
    text-align: center;
    margin-bottom: 52px;
  }
  @include e(heading) {
    font-weight: 700;
    font-size: 24px;
    line-height: 33px;
    color: $orange;
    margin-bottom: 20px;
  }
  @include e(description) {
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    color: $white;
  }
  @include e(frame) {
    width: 100%;
    max-width: 430px;
    height: 270px;
  }
}
