.statistics {
  background: rgba(29, 26, 20, 1);
  @include e(case) {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
    padding: 65px 0 65px 0;
    width: 100%;
    margin: 0 auto;
  }
  @include e(unit) {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width: calc((100% / 2) - 5px);
  }
  @include e(num-box) {
    font-weight: 700;
    font-size: 64px;
    line-height: 75px;
    color: $orange;
  }
  @include e(text) {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: $white;
    margin: 0;
    @include m(bold) {
      font-weight: 700;
    }
  }
  @include e(img-box) {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 75px;
    height: 75px;
  }
  @include e(img) {
    width: 47px;
    height: 47px;
  }
}
