.footer {
  @include e(case) {
    display: flex;
    padding: 75px 0;
    max-width: 1060px;
    margin: 0 auto;
  }
  @include e(logo-box) {
    margin-right: 30px;
  }
  @include e(elements) {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 20px;
    padding: 0 20px 0 0;
  }
  @include e(list-2) {
    border: none;
  }
}
