.galery {
  @include e(btn-box) {
    display: none;
  }
  @include e(heading) {
    font-size: 48px;
    line-height: 56px;
    margin: 0 0 53px;
  }
}
.grid-box {
  margin: 0 auto 50px;
  @include e(unit) {
    margin: 0 5px;
  }
  @include e(img) {
    max-width: 454px;
  }
}
.caption-1 {
  font-size: 48px;
  line-height: 48px;
}
.caption-2 {
  font-size: 30px;
  line-height: 35px;
}
