.content {
  @include e(box) {
    max-width: 1060px;
    grid-template-columns: 2fr 1fr;
    grid-template-rows: 1fr 1fr;
  }
  @include e(title) {
    grid-column: 1;
    grid-row: 1;
  }
  @include e(block) {
    grid-column: 1;
    grid-row: 2;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    margin: 0;
  }
  @include e(text) {
    grid-column: 1;
    grid-row: 2;
    margin: 0;
    align-self: flex-start;
    width: 100%;
    max-width: 520px;
  }
}
.order {
  grid-column: 2;
  grid-row: 1/3;
}
.popup {
  @include e(content) {
    padding: 15px 90px 20px;
  }
  @include e(radio-item) {
    &:hover {
      cursor: pointer;
    }
  }
}
